html {
    box-sizing: border-box;
}

.container {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-right: 20px;
    padding-left: 20px;

    @media screen and (min-width: $tablet) {
        width: $tablet;
        padding-right: 16px;
        padding-left: 16px;
    }

    @media screen and (min-width: $desktop) {
        width: $desktop;
    }
}

body {
    font-family: 'DM Sans', sans-serif;
    @include font-set (16px, 500, 1.75);
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

a {
    text-decoration: none;
    transition: color var(--duration) var(--timing-function);
}

ul, ol {
    list-style: none;
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 0;
}

button {
    border: none;
    outline: none;
    cursor: pointer;
}

.button {
    height: 44px;
    border-radius: 22px;
    background-color: var(--primary-white-color);

    @include font-set(null, 700, 1.31);
    color: var(--dark-pink-color);
    text-align: center;
}
